/* Banner */

.banner {
    background: url('../images/banner.png') no-repeat;
    background-position: center center;
    background-size: cover;
    height: 840px;
    color: $secondary-color;
    /* margin-top: 108px; */
  }
  
  .banner .slogan {
    transition: all .5s linear;
  }
  
  .banner .slogan {
    text-align: right;
    font-size: 36px;
    line-height: 54px;
  }
  
  .banner .slogan .slogan__title {
    font-size: 64px;
    line-height: 96px;
  }
  
  @media (max-width: 767px) {
    .banner .slogan {
      margin-right: 48px;
    }
  
    .banner .slogan .slogan__subTitle {
      font-size: 20px;
      line-height: 30px;
    }
  
    .banner .slogan .slogan__title {
      font-size: $font-size-b;
      line-height: 72px;
    }
  }
  
  @media (max-width: 610px) {
    .banner {
      margin-top: 50px;
    }
  
    .banner .slogan .slogan__title {
      font-size: 40px;
    }
  }
  
  @media (max-width: 375px) {
    .banner .slogan {
      margin-right: 12px;
    }
  
    .banner .slogan .slogan__subTitle {
      font-size: 16px;
    }
  
    .banner .slogan .slogan__title {
      font-size: $font-size-m;
    }
  }
  
  /* BusinessItem */
  
  .businessItem {
    padding: 64px 0 80px 0;
    background-color: $background-color;
  }
  
  .businessItem .title {
    line-height: 72px;
    font-size: $font-size-b;
    color: $secondary-color;
    margin-bottom: 44px;
  }
  
  .businessItem ul {
    padding: 0 48px;
  }
  
  .businessItem ul li {
    text-align: center;
    transition: all .5s linear;
    width:22.5%;
    /* width: calc(100% / 4 - 24px); */
  }
  
  .businessItem ul li:last-child {
    margin-right: 0;
  }
  
  .businessItem .icon {
    margin: 0 auto;
    margin-bottom: 40px;
  }
  
  .businessItem .subTitle {
    line-height: 36px;
    font-size: $font-size-s;
    color: $secondary-color;
    margin-bottom: 4px;
  }
  
  .businessItem .dec {
    line-height: 24px;
    font-size: 16px;
  }
  
  @media (max-width: 767px) {
    .businessItem{
      padding-top: 40px;
      padding-right: 48px;
      padding-left: 48px;
      padding-bottom: 36px;
    }
    .contentBox{
      max-width:672px;
    }
    .businessItem ul li {
      width: 45%;
      margin-bottom: 12px;
    }
  
    .businessItem ul .moneyLi {
      order: 3;
    }
  
    .businessItem ul .timeLi {
      order: 1;
    }
  
    .businessItem ul .serviceLi {
      order: 2;
    }
  
    .businessItem ul .glassesLi {
      order: 4;
    }
  
    .businessItem .title {
      font-size: $font-size-m;
      margin-bottom: 32px;
    }
  
    .businessItem .subTitle {
      line-height: 30px;
      font-size: 20px;
      margin-bottom: 8px;
    }
  }
  
  @media (max-width: 375px) {
    .businessItem{
      padding-top: 32px;
      padding-bottom: 48px;
      padding-left: 12px;
      padding-right: 12px;  
    }
    .contentBox{
      max-width:351px;
    }
    .businessItem .title {
      font-size: $font-size-s;
    }
    
  
    .businessItem ul li {
      width: 100%;
      padding-top: 20px;
      padding-bottom: 8px;
      margin-bottom: 0;
    }
  }
  /* Collection */
  
  .collection {
    padding-top: 64px;
    padding-bottom: 80px;
  }
  
  .collection .title {
    line-height: 72px;
    font-size: $font-size-b;
    color: $secondary-color;
    margin-bottom: 60px;
  }
  
  .collection .contentBox {
    max-width: 1190px;
    margin: 0 auto;
  }
  
  .collection ul li {
    margin-bottom: 32px;
  }
  .collection ul li:last-child{
    margin-bottom: 0;
  }
  
  .collection li .herfImg {
    cursor: default;
    max-width: 792px;
    display:block;
  }
  .collection ul li .product-2-hover {
    position: absolute;
    width: 796px;
    top: 0;
    z-index: -1;
  }
  
  .collection ul li .herfImg:hover .product-2-hover {
    z-index: 1;
  }
  
  .collection ul li .item__dec .icon-diamond-white,
  .collection ul li .item__dec .icon-sun-white,
  .collection ul li .item__dec .icon-thunder-white {
    display: none;
  }
  
  .collection ul .item {
    display: flex;
    align-items: center;
  }
  
  .collection ul li:nth-child(2) .item {
    flex-direction: row-reverse;
  }
  
  .collection .item .icon {
    width: 48px;
    height: 48px;
    margin-right: 8px;
  }
  
  .collection .item .item__dec {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 24px;
    max-width:354px;
  }
  
  .collection ul li:nth-child(2) .item__dec {
    margin-left: 0;
    margin-right: 24px;
  }
  
  .collection .item .item__dec__text {
    line-height: 72px;
    font-size: $font-size-b;
    color: $primary-color;
  }
  @media (max-width: 767px) {
    .collection{
      padding:40px 48px;
    }
    .collection .contentBox{
      max-width:672px;
    }
    .collection ul li{
      margin-bottom: 8px;
    }
    .collection ul li .item .herfImg{
      width:60%;
    }
    .collection ul li .item .item__dec{
      margin-left: 0;
      width:40%;
    }
    .collection ul li:nth-child(2) .item {
      flex-direction: row;
    }
    .collection ul li .item .item__dec{
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .collection .item .item__dec__text {
      line-height: 42px;
      font-size: 28px;
    }
  }
  
  @media (max-width: 375px) {
    .collection{
      padding: 24px 12px;
    }
    .collection .contentBox{
      max-width:351px;
    }
    .collection .title{
      margin-bottom: 16px;
      font-size: $font-size-s;
      line-height: 48px;
      font-weight:bolder;
    }
    .collection ul li .item .herfImg{
      display: none;
    }
    .collection ul li .item .item__dec {
      padding-top:50px;
      padding-bottom: 50px;
      width: 100%; 
      height: 134px;
    }
  
    .collection ul li .item__dec .item__dec__text {
      color: #FFFFFF;
    }
  
    .collection ul li .item__dec .icon-diamond,
    .collection ul li .item__dec .icon-sun,
    .collection ul li .item__dec .icon-thunder {
      display: none;
    }
  
    .collection ul li .item__dec .icon-diamond-white,
    .collection ul li .item__dec .icon-sun-white,
    .collection ul li .item__dec .icon-thunder-white {
      display: inline-block;
    }
  
    .collection ul li:first-child .item__dec {
      background: url('../Images/product-1.png') no-repeat;
      background-position: center center;
      background-size: cover;
    }
  
    .collection ul li:nth-child(2) .item__dec {
      background: url('../Images/product-2.png') no-repeat;
      background-position: center center;
      background-size: cover;
    }
  
    .collection ul li:nth-child(3) .item__dec {
      background: url('../Images/product-3.png') no-repeat;
      background-position: center center;
      background-size: cover;
    }
  
    .collection ul li .item__dec {
      flex-basis: 100%;
      margin-left: 0;
    }
  
    .collection ul li:nth-child(even) .item__dec {
      margin-left: 0;
    }
  
    .collection ul li .herfImg:hover .product-2-hover {
      z-index: -1;
    }
