  /* CollectionSecond */
  
  .collectionSecond {
    position: relative;
    padding-top: 64px;
    background: url('../images/bg.png') no-repeat;
    background-position: center;
    background-size: cover;
  }
  
  .collectionSecond .title {
    line-height: 72px;
    font-size: $font-size-b;
    color: #FFFFFF;
    margin-bottom: 60px;
  }
  
  .collectionSecond .album {
    margin-bottom: 60px;
    padding: 0 48px;
  }
  
  .collectionSecond .album .first {
    margin-right: 8px;
    flex-shrink: 1;
  }
  
  .collectionSecond .album .second {
    flex-shrink: 2;
  }
  
  .collectionSecond .album .second__img {
    margin-bottom: 8px;
  }
  
  .collectionSecond .album .fourth {
    margin-left: 8px;
  }
  
  .collectionSecond .album .fourth__img {
    margin-bottom: 8px;
  }
  
  .collectionSecond .moreArea {
    height: 167px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  
  .collectionSecond .btnArea {
    position: relative;
    padding-right: 84px;
  }
  
  .collectionSecond .btnArea .btnArea__btn {
    position: absolute;
    background: #FFFFFF;
    top: 50%;
    margin-top: -52.5px;
    width: calc(100% - 21px);
    left: 22px;
  }
  
  .collectionSecond .btnArea .btnArea__btn__text {
    line-height: 108px;
    font-size: 72px;
    color: $primary-color;
    padding-left: 32px;
    margin-right: 17px;
  }
  
  .collectionSecond .btnArea .btnArea__btn__text:before {
    content: 'MORE';
    position: absolute;
    color: $primary-color;
    top: 0;
    transition: top .5s;
  }
  
  .collectionSecond .btnArea .btnArea__btn:hover .btnArea__btn__text:before {
    top: -94px;
  }
  
  .collectionSecond .btnArea .btnArea__btn__text:after {
    content: 'MORE';
    position: absolute;
    color: $primary-color;
    transition: top .5s;
    top: 0;
    left: 32px;
  }
  
  .collectionSecond .btnArea .btnArea__btn:hover .btnArea__btn__text:after {
    top: -156px;
  }
  
  .collectionSecond .btnArea .icon {
    position: relative;
    display: inline-block;
    width: 48px;
    height: 48px;
    right: 0;
    transition: right .5s;
  }
  
  .collectionSecond .btnArea .btnArea__btn:hover .icon {
    right: -20px;
  }
  
  .collectionSecond .albumSecond {
    margin-bottom: 144px;
    padding: 0 50px;
  }
  
  .collectionSecond .albumSecond .imgText {
    position: relative;
    width: 585px;
    height: 592px;
    white-space: nowrap;
    overflow: hidden;
  }
  
  .collectionSecond .albumSecond .imgText.doubleA {
    background: url('../images/product-8.png') no-repeat;
    background-position: center;
    background-size: cover;
    margin-right: 30px;
  }
  
  .collectionSecond .albumSecond .imgText.youth {
    background: url('../images/product-9.png') no-repeat;
    background-position: center;
    background-size: cover;
  }
  
  .collectionSecond .albumSecond .subTitle {
    line-height: 108px;
    font-size: 72px;
    color: $primary-color;
    z-index: 1;
  }
  
  .collectionSecond .albumSecond .filter {
    position: absolute;
    width: 100%;
    height: 100%;
    background: hsla(0,0%,100%,.3);
  }
  
  .collectionSecond .titleTwo {
    padding-bottom: 268px;
  }
  
  @media (max-width: 900px) {
    .collectionSecond .btnArea .btnArea__btn__text {
      line-height: 30px;
      font-size: 40px;
    }
  }
  
  @media (max-width: 767px) {
    .collectionSecond .btnArea .icon {
      width: 27px;
      height: 27px;
    }
  
    .collectionSecond .title {
      line-height: 48px;
      font-size: $font-size-m;
      color: #FFFFFF;
      margin-bottom: 32px;
    }
  
    .collectionSecond .titleTwo {
      padding-bottom: 332px;
      font-size: $font-size-m;
      line-height: 48px;
    }
  
    .collectionSecond .albumSecond {
      margin-bottom: 40px;
    }
  
    .collectionSecond .albumSecond .imgText {
      width: 322px;
      height: 280px;
    }
  
    .collectionSecond .albumSecond .subTitle {
