/* Menu */

.menu {
  height: 108px;
  background-color: $primary-color;
  padding: 0 88px;
  transition: all .5s linear;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  ul li{
    line-height: 30px;
    margin-left: 64px;
  }
  .icon-menu{
    position: absolute;
    left: 16px;
    display: none;
  }
}
.menu.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

@media (max-width: 767px) {
  .menu {
    padding: 0 48px;
  }

  .menu ul li {
    margin-left: 40px;
  }
}

@media (max-width: 610px) {
  .menu {
    height: 50px;
    justify-content: center;
  }

  .menu ul {
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    max-height: 0;
    transition: all 0.8s;
    overflow: hidden;
    z-index: 120;
    flex-direction: column;
    text-align: center;
  }

  .menu.active ul {
    max-height: 250px;
  }

  .menu ul li {
    margin-left: 0;
    height: 60px;
    width: 100%;
    line-height: 60px;
    background-color: $primary-color;
    border-top: 1px solid #FFFFFF;
  }

  .menu .img {
    width: 48px;
    height: 29px;
  }

  .menu .icon-menu {
    display: block;
  }
}

@media (max-width: 375px) {
  .menu {
    justify-content: center;
  }

  .menu ul {
    display: none;
  }
}

.menu ul li:first-child {
  margin-left: 0;
}

/* Footer */

.footer {
  padding-top: 36px;
  background-color: $primary-color;
  color: #FFFFFF;
  .container{
    max-width: 1150px;
    margin: 0 auto;
    padding: 0 53px; 
  }
  .infos .menu__ul{
    height: 48px;
    line-height: 48px;
    .li{
      margin-right: 48px;
    }
    .hrefText{
      line-height: 24px;
      font-size: 16px;
    }
  }
  .infosTwo{
    margin-top: 18px;
    .imgTextUl .icon{
      width: 48px;
      height: 48px;
      display: inline-block;
      margin-right: 12px;
    }
    .imgTextUl .li{
      display: flex;
      align-items: center;
    }
    .imgTextUl .text{
      line-height: 54px;
      font-size: 36px;
      text-decoration: none;
      color:white;
    }
  }
}

.footer .icons .ul .li {
  margin-right: 32px;
}

.footer .icons .ul .li:last-child {
  margin-right: 0;
}

.footer .icons .ul .li .icon {
  width: 48px;
  height: 48px;
}

.footer .hr {
  margin: 24px 0;
}

@media (max-width: 780px) {
  .footer .infos .menu__ul .li {
    margin-right: 40px;
  }

  .footer .icons .ul .li .icon {
    width: 24px;
    height: 24px;
  }
}

@media (max-width: 767px) {
  .footer .infosTwo .imgTextUl .text {
    line-height: 30px;
    font-size: 20px;
  }
}

.footer .infos .menu__ul .li:last-child {
  margin-right: 0;
}

@media (max-width: 680px) {
  .footer .infos .menu__ul {
    display: none;
  }

  .footer .infosTwo {
    order: -1;
  }

  .footer .infosTwo .imgTextUl .icon {
    width: 20px;
    height: 20px;
  }
}

@media (max-width: 375px) {
  .footer .container {
    padding: 0 11px;
  }

  .footer .infosTwo .imgTextUl .text {
    font-size: 16px;
    line-height: 30px;
  }

  .footer .icons .ul .li {
    margin-right: 24px;
  }

  .footer .infosTwo .imgTextUl .icon {
    margin-right: 8px;
  }
}

/* copyright */

.copyright {
  padding-bottom: 40px;
   .imgText .img{
    margin-right: 38px;
   }
   .imgText .text{
    line-height: 24px;
    font-size: 16px;
   }
   .ul .li{
    margin-right: 62px;
   }
   .ul .li:last-child{
    margin-right: 0;
   }
}

@media (max-width: 767px) {
  .copyright {
    padding-bottom: 28px;
  }

  .copyright .imgText .img {
    display: none;
  }

  .copyright .ul .li {
    margin-right: 24px;
  }
}

@media (max-width: 375px) {
  .copyright {
    flex-wrap: wrap;
    padding-bottom: 8px;
  }

  .copyright .imgText {
    width: 100%;
    font-size: 14px;
    line-height: 21px;
  }

  .copyright .ul {
    flex-wrap: wrap;
  }

  .copyright .ul .li {
    width: 100%;
    margin-bottom: 8px;
  }

  .copyright .ul .li:last-child {
    margin-bottom: 0px;
    padding-bottom: 24px;
  }
}