.faq-banner{
    background:url('../images/faq-banner.png') no-repeat;
    background-position: center center;
    background-size: cover;
    height: 304px;
    padding-top: 214px;
        .faq-banner-content{
            font-weight:$font-weight;
            font-size:$font-size-xl;
            align-items: center;
            line-height: 80px;
            img{
                max-width:48px;
            }
        }
}
@media(max-width:767px){
    .faq-banner{
        background:url('../images/pad-faq-banner.png') no-repeat;  
        padding-top: 227px;
        z-index:1;
        .faq-banner-content{
            font-size: $font-size-b;
            line-height: 80px;
        }
    }

}
@media(max-width:374px){
    .faq-banner{
        height: 143px;
        background:url('../images/phone-faq-banner.png') no-repeat;
        padding-top: 90px;
        .faq-banner-content{
            font-size: 28px;
            img{
                max-width:24px;
            }
        }
    }
}

.faq-content{
    padding-top: 88px;
    padding-bottom: 156px;
    .faq-title{
        font-size:32px;
        line-height: 63px;
        font-weight:bold;
    }
    h3{
        border:1px solid black;
        padding-top: 22px;
        padding-bottom: 22px;
        padding-left: 3.1%;
        padding-right: 3.1%;
        font-size: 20px;
        line-height: 23px;
        font-weight:bold;
    }
    h3:hover{
        background: $primary-color;
        color:white;
    }
    h3.active{
        background: $primary-color;
        color:white;
    }
    p{
        border:1px solid black;
        font-size: $font-size-sl;
        line-height: 24px;
        padding-top: 22px;
        padding-bottom: 22px;
        padding-left: 3.1%;
        padding-right: 3.1%;
        background: #F2F2F2;
        display:none;
    }
}

@media(max-width:767px){
    .faq-content{
        padding-top: 40px;
        padding-bottom: 104px;
        .contentBox{
            max-width:672px;
            h3{
                font-size: 16px;
                line-height: 24px;
                padding-top: 16px;
                padding-bottom: 16px;
            }
            p{
                font-size: 14px;
                line-height: 21px;
                padding-top: 16px;
                padding-bottom: 16px;
            }
        }
    }
}
@media(max-width:374px){
    .faq-content{
        padding-top: 24px;
        padding-bottom: 56px;
        .contentBox{
            max-width:350px;
            h3{
                padding-right:4%; 
                padding-left:4%;    
            }
        }
    }
}
